.heartpluse {
  background: red;
  position: relative;
  left: 70px;
  top: 50px;
  height: 30px;
  width: 30px;
  /* Animation */
  transform: rotate(-45deg) scale(1);
  animation: pulse 2s linear infinite;
}

.heartpluse::after {
  /* background:blue; */
  background: inherit;
  border-radius: 50%; /* To make circle */
  content: "";
  position: absolute;
  /* top: -100px;*/
  top: -50%; /* Inherit properties of parent */
  /* left: -100px; */
  left: 0;
  height: 30px;
  width: 30px;
}
.heartpluse::before {
  /*  background:green; */
  background: inherit;
  border-radius: 50%; /* To make circle */
  content: "";
  position: absolute;
  top: 0;
  right: -50%; /* Inherit properties of parent */
  height: 30px;
  width: 30px;
}

@keyframes pulse {
  0% {
    transform: rotate(-45deg) scale(1);
    opacity: 0;
  } /*
  10% {
      transform: rotate(-45deg) scale(1.3);
  }
  20% {
      transform: rotate(-45deg) scale(0.9);
  }
  30% {
      transform: rotate(-45deg) scale(1.2);
  }
  40% {
      transform: rotate(-45deg) scale(0.9);
  }*/
  50% {
    transform: rotate(-45deg) scale(1.3);
    opacity: 1;
  } /*
  60% {
      transform: rotate(-45deg) scale(0.95);
  }
  70% {
      transform: rotate(-45deg) scale(1);
  } */
  100% {
    transform: rotate(-45deg) scale(1);
    opacity: 1;
  }
}

