.circle-wrap {
  margin: 50px auto;
  /* width: 150px;
  height: 150px; */
  background: #2c2f33;
  box-shadow: inset -16.5697px -16.5697px 41.4243px #3b4451,
    inset 16.5697px 16.5697px 41.4243px #000000;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  left: 20px;
}

.circle-wrap .circle .mask,
.circle-wrap .circle .fill {
  width: 100px;
  height: 100px;

  position: absolute;
  border-radius: 50%;
}

.circle-wrap .circle .mask {
  clip: rect(0px, 150px, 150px, 75px);
}

.circle-wrap .inside-circle {
  width: 80px;
  height: 80px;
  left: 18.5px;
  top: 48px;
  border-radius: 50%;
  background: #2c2f33;
  box-shadow: inset -16.5697px -16.5697px 41.4243px rgba(59, 68, 81, 0.5),
    inset 16.5697px 16.5697px 41.4243px rgba(0, 0, 0, 0.5);
  text-align: center;
  margin-top: 14px;
  margin-left: 14px;
  color: #ffff;
  position: absolute;
  z-index: 100;
}

