/* color animation */

/* 3rd progress bar */
.mask .fill {
  clip: rect(0px, 100px, 100px, 0px);
  background-color: #20dde9;
  border-radius: 50px;
}

.mask.full,
.circle .fill {
  animation: fill ease-in-out 3s;
  transform: rotate(100deg);
}

@keyframes fill {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(100deg);
  }
}

