body {
  position: relative;
  background: #2c2f33;
  position: fixed;
}

.green_shade {
  position: absolute;
  width: 480px;
  height: 345px;
  left: -27px;
  top: -52px;

  background: linear-gradient(
    142.25deg,
    #5264f9 39.86%,
    rgba(58, 249, 239, 0.85) 78.86%
  );
  border-radius: 50%;
}

.black_circle {
  position: absolute;
  width: 356px;
  height: 354px;
  left: -55px;
  top: -118px;
  background: #2c2f33;
  box-shadow: -6.22302px -6.22302px 18.6691px #3b4451,
    6.22302px 6.22302px 18.6691px #000000;
  border-radius: 50%;
}

.purple_circle {
  position: absolute;
  width: 356px;
  height: 354px;
  left: -56px;
  top: -52px;
  background: linear-gradient(
    142.25deg,
    #2f56f8 53.78%,
    rgba(198, 48, 248, 0) 96.32%
  );
  border-radius: 50%;
}

p {
