  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;

  color: #ffffff;
}

.sign_in {
  position: absolute;
  width: 315px;
  height: 72px;
  left: 23px;
  top: 460px;

  background: linear-gradient(93.94deg, #25cede 7.43%, #1ea6b3 95.06%);
  border-radius: 28px;

  font-family: "M PLUS 1";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 29px;
  /* identical to box height */
  text-align: left;

  color: #ffffff;
  padding-left: 20px;
  border-color: #25cede;

  filter: drop-shadow(6.22px 6.22px 18.67px #000000)
    drop-shadow(-6.22px -6.22px 18.67px #3b4451);
}

.mini_circle {
  position: absolute;
  width: 60px;
  height: 70px;
  border-top-left-radius: 100px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 80px;
  border-top-right-radius: 110%;

  left: 254px;
  top: 0px;
  background: linear-gradient(
    134.93deg,
    rgba(82, 100, 249, 0.51) 19.24%,
    rgba(58, 249, 239, 0.77) 44.19%
  );
}

.signintext {
  position: absolute;
  width: 73px;
  height: 24px;
  top: 1px;
  left: 1px;
  padding-left: 20px;

  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  /* identical to box height */

  color: #25cede;
}
.sign_up {
  position: absolute;
  width: 315px;
  height: 72px;
  left: 23px;
  top: 570px;

  filter: drop-shadow(6.22px 6.22px 18.67px #000000)
    drop-shadow(-6.22px -6.22px 18.67px #3b4451);

  background: none;
  border-radius: 28px;
  border-color: rgba(58, 249, 239, 0.77);
}

.arrow {
  position: absolute;
  left: 90.58%;
  top: 37.91%;
  margin-right: 29px;
  color: rgba(58, 249, 239, 0.77);
}

.arrow1 {
  color: white;
  left: 20px;
  top: 20px;
}

